<template>
  <div class="column is-3">
    <div id="tile" v-on:click="selectImage">
      <figure class="image is-4by3">
        <img id="thumbnail" v-bind:src=asset.url>
      </figure>
      <div class="content title">{{ asset.title }}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    asset: Object,
  },
  methods: {
    selectImage() {
      this.$store.commit({
        type: 'setSelectedAsset',
        asset: this.asset,
      });
    },
  },
};
</script>

<style>

#tile {
  background-color: #ccc;
  padding: 10px;
  border-radius: 5px;
}

#thumbnail {
  object-fit: contain;
  border-radius: 5px;
  background-color: #aaa;
}
</style>
