<template>
  <div class="tile is-parent">
    <div class="tile is-child">
      <input
        class="input"
        v-on:keyup.enter="searchForAssets"
        v-model="searchText"
        type="text"
        placeholder="Search..."
      />
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    searchText: '',
  }),
  methods: {
    searchForAssets() {
      this.$store.dispatch({
        type: 'searchForAssets',
        searchText: this.searchText,
      });
    },
  },
};
</script>

