<template>
  <div class="tile is-ancestor is-mobile" id="container">
    <div class="tile is-vertical is-parent is-3">
      <div class="tile is-child box">
        <div class="title is-6">{{ asset.title }}</div>
        <div>{{ asset.uid }}</div>
      </div>
      <div class="tile is-child box">
        <div class="title is-6">Focal Point</div>
        <div>x: {{ focalPoint.x }}</div>
        <div>y: {{ focalPoint.y }}</div>
      </div>
      <div class="button is-danger box" v-on:click="clearSelectedImage">
        Select another image.
      </div>
    </div>
    <div class="tile is-parent">
      <div class="tile is-child image">
        <SelectedImage id="bigImage"/>
      </div>
    </div>
  </div>
</template>

<script>
import SelectedImage from './SelectedImage.vue';

export default {
  computed: {
    asset() {
      return this.$store.state.selectedAsset;
    },
    focalPoint() {
      const fp = this.$store.state.focalPoint;

      return {
        x: parseFloat(fp.x).toFixed(3),
        y: parseFloat(fp.y).toFixed(3),
      };
    },
  },
  components: {
    SelectedImage,
  },
  methods: {
    clearSelectedImage() {
      this.$store.commit({
        type: 'setSelectedAsset',
        asset: null,
      });
    },
  },
};
</script>

<style>

#container {
  height: 100%;
}

#bigImage {
  height: 100%;
  width: 100%;
  background-color: #888;
  border-radius: 8px;
}

</style>
